.about_me_container {
  text-align: center;
  padding-top: 50px;
  width: 100%;
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about_me_container .inner_container{
  display: grid;
  width: 100%;
  grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  align-items: center;
  justify-items: center;
}

.about_me{
  grid-column: span 2;
}

.about_me_img{
  width: 18vw;
  height: 30vh;
  background-image: url("../images/about_me_img.jpg");
  background-size: cover;
  border-radius: 25%;
}

.about_me .name {
  font-size: 5vw;
}

.about_me #content {
  font-size: 2vw;
  color: #666;
  height: 20vh;
  width: 60vw;
  display: flex;
  align-items: center;
  justify-content: center;
}
h4{
  margin: 10px;
}

.sns_tag i {
  color: white;
  border-radius: 5vw;
  cursor: pointer;
  display: inline-block;
  font-size: 2.5vw;
  height: 5vw;
  line-height: 5vw;
  margin: 0 5px;
  position: relative;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  width: 5vw;
  transition: 0.2s ease-in;
}

.github i {
  background-color: #333;
}

.google i {
  background-color: #ea4335;
}


/* Hover Transition */
.google i:hover,
.github i:hover {
  background-color: black;
}

/* Media Query */
@media (max-width: 1000px) {
  .sns_tag {
    text-align: center;
  }
}
